body {
  background-color: #3F3D34;
    font-family: Raleway;
}
nav{
  padding-top: 40px;
}
.nav-title{
  font-family: Arizonia;
  font-size: 125%;
}


.app-body{
  background-color: white;
  padding-top: 30px;
  padding-bottom: 30px;
  display: -ms-flexbox;
  display: flex;;
}

.offset{
  position: relative;
  bottom: 80px;
  -webkit-box-shadow:  rgba(0, 0, 0, 0.22) -2px 9px 5px 0px;
          box-shadow:  rgba(0, 0, 0, 0.22) -2px 9px 5px 0px;
}
.headings {
  font-family: Raleway;
  font-weight: bold;
}
.sub-headings {
  font-style: italic;
  color: #9E9E9E;
}

.fa {
  font-size: 1.3em;
}

.card{
  padding: 20px;
  margin-top: 20px;
  -webkit-box-shadow: -1px 4px 5px #9E9E9E;
          box-shadow: -1px 4px 5px #9E9E9E;
}

.tiles{
  margin: 0 auto;
  text-align: center;
}

footer{
  text-align: center;
  color: #fff;
  padding-bottom: 20px;
}

.btn-primary{
  color: #FFEB3B;
  background-color: #000000;
  border-color: #9E9E9E;
}
.list-group-item:hover{
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}
.thumbnail:hover{
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}
.thumbnail{
  -webkit-box-shadow: 0px 3px 5px rgba(128, 128, 128, 0.43);
          box-shadow: 0px 3px 5px rgba(128, 128, 128, 0.43)
}
.loader {
    border: 10px solid #f3f3f3;
    border-top: 10px solid yellow;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


/* General styles and layout */
.starwars {
  height: 17em;
  left: 50%;
  position: absolute;
  top: 53%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 34em;
}

.byline span {
  display: inline-block;
}

img {
  width: 100%;
}


.byline {
  position: absolute;
  color: #fff;
  font-family: "Raleway";
  font-size: 3.25em;
  left: -2em;
  letter-spacing: 0.4em;
  right: -2em;
  text-align: center;
  text-transform: uppercase;
  top: 29%;
}

/*** Media queries for adjusting to different screen sizes ***/

@media only screen and (max-width: 600px) {
  .starwars {
    font-size: 10px;
  }
}

@media only screen and (max-width: 480px) {
  .starwars {
    font-size: 7px;
  }
}



